import React, { useState, useEffect, useRef } from "react";
import projects from "../data/projects";
import "../styles/work.css";

const Work = () => {
  const mouseEnter = id => {
    document.getElementById(id).style.animation = "slideUp .3s";
    document.getElementById(id).style.display = "flex";
  };

  const mouseLeave = id => {
    document.getElementById(id).style.display = "none";
  };

  const list = projects.map(item => {
    return (
      <div
        className="card"
        key={item.number}
        onMouseEnter={e => mouseEnter(item.id)}
        onMouseLeave={e => mouseLeave(item.id)}
      >
        <img src={item.img} alt={item.name + " project preview picture"} />
        <div className="project-info">
          <div className="title">
            <h2>{item.name}</h2>
          </div>
          <div id={item.id} className="text">
            <p>{item.description}</p>
            <ul>
              {item.features.map(feat => {
                return <li>{feat}</li>;
              })}
            </ul>
            <a href={item.linkProject} target="_blank">
              LIVE
            </a>
            <a href={item.linkCode} target="_blank">
              CODE
            </a>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div id="work">
      <h1>My projects</h1>
      <div className="card-box">{list}</div>
    </div>
  );
};

export default Work;
