import React, { useState, useEffect, useRef } from "react";
import contacts from "../data/contacts";
import "../styles/contact.css";

const Contact = () => {
  let list = contacts.map(item => {
    return item.id == 3 ? null : item.id == 4 ? null : (
      <li key={item.id}>
        <a href={item.link} target="_blank">
          <div className="logo">
            <img src={item.img} />
          </div>
        </a>
      </li>
    );
  });

  return (
    <div id="contact">
      <h1>Want to build something together?</h1>
      <p>
        <br />
        Feel free to message me directly from here or via social network:
      </p>
      <ul>{list}</ul>
      <div id="form">
        <form
          className="send-form"
          method="POST"
          action="https://script.google.com/macros/s/AKfycbxM3itgwvArllKDNmk-KknYIf6sIWse-HcLtScT/exec"
        >
          <input id="name" name="name" placeholder="NAME" />

          <input
            id="email"
            name="email"
            type="email"
            required
            placeholder="E-MAIL"
          />

          <textarea
            id="message"
            name="message"
            rows="6"
            placeholder="MESSAGE"
          ></textarea>
          <button id="send" type="submit">
            Send
          </button>
        </form>
      </div>
    </div>
  );
};

export default Contact;
